<template>
  <v-row class="flex-column">
    <v-col class="px-6 mb-3">
      <v-card 
        dark
        :style="!$vuetify.theme.dark ? 'background: #57707D' : 'background: #424242'"
      >
        <v-row class="pa-4">
          <v-col>
            <v-avatar
              color="grey"
              size="108"
            >
              <v-img src="https://cdn.vuetifyjs.com/images/john.jpg"></v-img>
            </v-avatar>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title class="title">{{ user.userName }}</v-list-item-title>
                <v-list-item-subtitle>Id: {{ id }}</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-col>
          <v-col 
            cols="12"
            sm="6"
            class="mx-auto"
          >
            <v-text-field
              v-model="user.userName"
              label="Имя"
              required
            ></v-text-field>

            <v-text-field
              v-model="user.balance"
              label="Баланс"
              required
            ></v-text-field>
            
            <v-select
              v-model="user.userRole"
              :items="['admin', 'moderator', 'user']"
              label="Роль"
              required
            ></v-select>
          </v-col>
        </v-row>
      </v-card>
    </v-col>
    <v-col class="px-6 mb-3">
      <v-card elevation="0">
        <v-card-title>
          <v-row justify="space-between">
            <v-col cols="12" sm="4">
              Ввод и вывод
            </v-col>
            <v-col cols="12" sm="4">
              <v-select
                label="Категория" 
                :items="['All', 'Deposit', 'Withdraw']"
                v-model="group_type2"
              ></v-select>
            </v-col>
            <v-col cols="12" sm="4">
              <v-text-field
                v-model="search2"
                append-icon="mdi-magnify"
                label="Поиск"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-title>
        <v-dialog 
          v-model="dialog" 
          max-width="540px"
        >
          <v-card>
            <v-card-title>
              {{ dialogTitle }}
              <v-spacer></v-spacer>
              <v-btn 
                color="error"
                icon
                :loading="loading"
                @click="checkWithdrawal()"
              >
                <v-icon>mdi-reload-alert</v-icon>
              </v-btn>
            </v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn 
                color="blue darken-1" 
                text
                @click="cancelWithdrawal()"
              >
                Отмена
              </v-btn>
              <v-btn
                color="blue darken-1" 
                text
                @click="acceptWithdrawal()"
              >
                Подтверждение
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-data-table
          :headers="headers2"
          :items="filteredItems2"
          :search="search2"
        >
          <template v-slot:item.action="{ item }">
            <v-btn 
              @click="openDialog(item, true)"
              icon
            >
              <v-icon small>
                mdi-check-circle-outline
              </v-icon>
            </v-btn>
            <v-btn 
              @click="openDialog(item)"
              icon
            >
              <v-icon small>
                mdi-close-circle-outline
              </v-icon>
            </v-btn>
          </template>
        </v-data-table>
      </v-card>
    </v-col>
    <v-col class="px-6 mb-3">
      <v-card elevation="0">
        <v-card-title>
          <v-row justify="space-between">
            <v-col cols="12" sm="4">
              История игр
            </v-col>
            <v-col cols="12" sm="4">
              <v-select
                label="Категория" 
                :items="['All', 'Winning', 'Losing', 'Process']"
                v-model="group_type"
              ></v-select>
            </v-col>
            <v-col cols="12" sm="4">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Поиск"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="filteredItems"
          :search="search"
        >
          <template v-slot:item.action>
            <v-btn icon>
              <v-icon small>
                mdi-close-circle-outline
              </v-icon>
            </v-btn>
          </template> 
        </v-data-table>
      </v-card>
    </v-col>
    <v-col class="px-6">
      <v-card elevation="0">
        <v-card-title>Общая статистика</v-card-title>
        <v-simple-table>
          <template v-slot:default>
            <thead>
              <tr>
                <th>Победы</th>
                <th>Поражения</th>
                <th>Депозит</th>
                <th>Вывод</th>
                <th>Баланс за все время</th>
                <th>Дата регистрации</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td v-for="(stat, index) of stats" :key="index">{{ stat }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>    
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  props: {
    id: {
      type: [Number, String],
      required: true
    }
  },
  data: () => ({
    dialog: false,
    dialogTitle: '',
    loading: false,
    group_type: null,
    group_type2: null,
    search: '',
    search2: '',
    headers: [
      { text: 'ID игры', align: 'left', value: 'gameId' },
      { text: 'Дата', value: 'date' },
      { text: 'Статус', value: 'status' },
      { text: 'Шанс победы', value: 'chanceOfVictory' },
      { text: 'Действия', value: 'action', sortable: false }
    ],
    headers2: [
      { text: 'Тип', align: 'left', value: 'type' },
      { text: 'Дата', value: 'date' },
      { text: 'Сумма', value: 'amount' },
      { text: 'Статус', value: 'status' },
      { text: 'Действия', value: 'action', sortable: false }
    ],
    items: [
      { 
        gameId: 1,
        date: new Date().toISOString().substr(0, 10),
        status: 'winning',
        chanceOfVictory: 0.4
      },
      { 
        gameId: 2,
        date: new Date().toISOString().substr(0, 10),
        status: 'losing',
        chanceOfVictory: 0.1   
      },
      { 
        gameId: 3,
        date: new Date().toISOString().substr(0, 10),
        status: 'winning',
        chanceOfVictory: 0.92 
      },
      { 
        gameId: 4,
        date: new Date().toISOString().substr(0, 10),
        status: 'process',
        chanceOfVictory: 'process'
      },
      { 
        gameId: 5,
        date: new Date().toISOString().substr(0, 10),
        status: 'winning',
        chanceOfVictory: 0.64 
      }        
    ],
    items2: [
      { 
        type: 'deposit',
        date: new Date().toISOString().substr(0, 10),
        status: 'done',
        amount: 400
      },
      { 
        type: 'deposit',
        date: new Date().toISOString().substr(0, 10),
        status: 'done',
        amount: 320
      },
      { 
        type: 'deposit',
        date: new Date().toISOString().substr(0, 10),
        status: 'done',
        amount: 150
      },
      { 
        type: 'withdraw',
        date: new Date().toISOString().substr(0, 10),
        status: 'done',
        amount: 1100
      },
    ],
    user: {
      userName: 'Marcus Obrien',
      userRole: 'moderator',
      balance: 1000
    },
    stats: {
      victory: 10,
      losing: 2,
      deposit: 1200,
      withdrawal: 2000,
      balance: 6500,
      date: new Date().toISOString().substr(0, 10)
    }
  }),
  computed: {
    filteredItems() {
      return this.items.filter((item) => {
        return (this.group_type == 'All') ? item : !this.group_type || (item.status.toLowerCase() === this.group_type.toLowerCase());
      });
    },
    filteredItems2() {
      return this.items2.filter((item) => {
        return (this.group_type2 == 'All') ? item : !this.group_type2 || (item.type.toLowerCase() === this.group_type2.toLowerCase());
      });
    },
  },
  methods: {
    openDialog(user, accept) {
      this.dialogTitle = accept ? 'Подтверждение заявки на вывод средств' : 'Отклонение заявки на вывод средств';
      this.dialog = true;
    },
    checkWithdrawal() {
      this.loading = true;
      setTimeout(() => {
        this.dialog = false;
        this.loading = false;
      }, 1000)
    },
    cancelWithdrawal() {
      this.dialog = false;
    },
    acceptWithdrawal() {
      this.dialog = false;
    }
  }
}
</script>
